import { Component } from '@angular/core'


@Component({
    selector: 'signup',
    template: require('./signup.html'),
    styles: [require('./signup.css')]
})
export class SignupComp {
    signup(event, username, passwd) {
        event.preventDefault();

        console.log(username+'<>'+passwd)
    }
}


